import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import Card from "./Card";
import Moviedata from "./moviedata";

ReactDOM.render(
  <>
    <h1>All Time Favourite Movies</h1>
    <header>
    {Moviedata.map((value)=>{
      return (
        <Card
        key={value.id}
        imgSrc={value.imgsrc}
        altName={value.imgsrc}
        title={value.sname}
        info={value.info}
        hrefLink={value.link}
      />
      );
    })}      
    </header>
  </>,
  document.getElementById("root")
);
