import React from "react";

function Card(props) {
  return (
    <div className="content">
      <div className="content-data">
        <div className="poster">
          <img className="image" src={props.imgSrc} alt={props.altName} />
        </div>
        <div className="details">
          <span className="movie_title">{props.title}</span>
          <span className="info">{props.info}</span>
        </div>
        <div className="watch_btn">
          <a href={props.hrefLink} target="_blank" rel="noreferrer">
            Watch now
          </a>
        </div>
      </div>
    </div>
  );
}

export default Card;
